.endgame {
  width: 100%;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.endgame__image {
  max-width: 516px;
  height: auto;
}

.endgame__text {
  margin: 9px;
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 35px;
  letter-spacing: 0.15px;
  color: #fff;
  opacity: 0.85;
  text-align: center;
}

.endgame__button {
  margin-top: 40px;
  padding: 10px 30px;
  border-radius: 4px;
  opacity: 0.85;
  background-color: #fff;
  outline: none;
  border: none;
  font-family: 'Open Sans';
  font-weight: bold;
  font-size: 16px;
  color: #1C7430;
}

.endgame__button:hover {
  opacity: 1;
  cursor: pointer;
}



