* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  background: -webkit-gradient(linear, left top, right top, from(#1c6a2d), color-stop(#1d7531), to(#1c6a2e));
  background: -webkit-linear-gradient(left, #1c6a2d, #1d7531, #1c6a2e);
  background: -o-linear-gradient(left, #1c6a2d, #1d7531, #1c6a2e);
  background: linear-gradient(90deg, #1c6a2d, #1d7531, #1c6a2e);
}

.container {
  margin: 0 auto;
  width: 60vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}



