.startgame {
  width: 100%;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.startgame__image {
  height: auto;
}

.startgame__name {
  margin: 20px;
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: 0.2px;
  text-transform: uppercase;
  color: #fff;
  opacity: 0.85;
}

.startgame__button {
  margin-top: 34px;
  padding: 10px 20px;
  outline: none;
  border: none;
  background-color: #fff;
  opacity: 0.85;
  border-radius: 4px;
  font-family: 'Open-Sans', sans-serif;
  font-size: 16px;
  font-weight: bold;
  line-height: 22px;
  color: #1C7430;
  letter-spacing: 0.6px;
}

.startgame__button:hover {
  opacity: 1;
  cursor: pointer;
}



